<template>
  <v-row class="pa-0 d-flex justify-center">
    <!-- <v-col cols="6" sm="8" md="4">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="date11"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-col cols="12" md="8">
            <v-text-field
              v-model="dates"
              label="ระบุเวลาในการดูแผนภูมิข้อมูล"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </v-col>
        </template>
        <v-date-picker
          :min="days.min"
          :max="days.max"
          v-model="date11"
          range
          locale="TH-th"
          no-title
          scrollable
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu = false">
            ยกเลิก
          </v-btn>
          <v-btn text color="primary" @click="requestData">
            ตกลง
          </v-btn>
        </v-date-picker>
      </v-menu>
    </v-col> -->

    <v-col cols="12" sm="12">
      <!-- input field -->
      <v-col cols="12" sm="3" v-if="this.currentUser">
        <v-row class="text-center" style="align-items:center">
          <v-file-input
            accept=".csv"
            style="width:4rem"
            show-size
            label="เลือกไฟล์เพื่อพยากรณ์"
            chips
            v-model="file"
          >
            <template v-slot:selection="{ text }">
              <v-chip small label color="#C4F57E">
                {{ text }}
              </v-chip>
            </template></v-file-input
          >

          <v-btn
            small
            class="ml-2"
            text
            color="primary"
            v-if="this.file"
            @click="submitFiles()"
            >ยืนยัน</v-btn
          >
        </v-row>
      </v-col>

      <!--End input field -->

      <v-row class="pl-6 ml-3">
        <p style="cursor:pointer" :class="{ active: loaded }">
          กราฟแสดงข้อมูลอุณหภูมิ
        </p>
        <v-spacer></v-spacer>
        <p
          v-if="!currentUser && !admin"
          style="cursor:pointer;font-size:0.9rem;color:red"
          class="pr-6"
          :class="{ active: loaded }"
          @click="$router.push('/register')"
        >
          กดที่นี่เพื่อพยากรณ์ด้วยตนเอง >>
        </p>
      </v-row>
      <v-col>
        <!-- <v-btn color="indigo" small outlined v-on:click="getDataall"
          >ทั้งหมด</v-btn
        > -->
        <!-- aert -->
        <v-snackbar v-model="snackbar" :timeout="timeout" :color="color" dense>
          {{ text }}

          <template v-slot:action="{ on, attrs }">
            <v-icon
              small
              color="white"
              text
              v-bind="attrs"
              @click="snackbar = false"
              v-on="on"
            >
              mdi-close
            </v-icon>
          </template>
        </v-snackbar>

        <!-- alert -->
        <v-card class="pa-4" style="background-color:rgb(255, 255, 255)">
          <div
            class="progress"
            v-if="!loadChart && NotData != 'ยังไม่มีข้อมูลการพยากรณ์'"
          >
            <v-progress-circular
              :size="60"
              :width="7"
              color="warning"
              indeterminate
            ></v-progress-circular>
          </div>
          <p
            class="progress"
            v-if="!loadChart && NotData != 'ยังไม่มีข้อมูลการพยากรณ์'"
          >
            กำลังโหลดข้อมูลกราฟ
          </p>
          <!-- notdata -->
          <v-list
            v-if="NotData != '' && numData.length < 1"
            class="text-center pa-2"
          >
            <v-icon color="warning" size="4rem">mdi-progress-close</v-icon>
            <p>{{ NotData }}</p>
          </v-list>
          <!-- end -->
          <apexchart
            v-if="loaded && loadChart && ChartData.date != ''"
            type="line"
            ref="chart"
            :MaxData="ChartData.max"
            :MinData="ChartData.min"
            :Date="ChartData.date"
          ></apexchart>
        </v-card>
      </v-col>
      <!-- ส่วนแดง ตารางและกราฟ -->
      <v-col>
        <div class="pa-3">
          <span
            style="cursor:pointer"
            :class="{ active: loaded }"
            class="align-self-center pl-2 pr-2"
            >ตารางสภาพอากาศและผลพยากรณ์</span
          >
        </div>
        <v-card>
          <div
            class="progress"
            v-if="!loadtable && NotDataTable != 'ยังไม่มีข้อมูลการพยากรณ์'"
          >
            <v-progress-circular
              :size="60"
              :width="7"
              color="warning"
              indeterminate
            ></v-progress-circular>
          </div>
          <p
            class="progress"
            v-if="!loadtable && NotDataTable != 'ยังไม่มีข้อมูลการพยากรณ์'"
          >
            กำลังโหลดข้อมูลตารางการพยากรณ์ ...
          </p>
          <!-- not data table -->
          <v-list
            v-if="NotDataTable != '' && forcast.length < 1"
            class="text-center pa-2"
          >
            <v-icon color="warning" size="4rem">mdi-progress-close</v-icon>
            <p>{{ NotDataTable }}</p>
          </v-list>
          <!-- not data table -->

          <Table v-if="loadtable && forcast != ''" :datatable="forcast" />
        </v-card>
      </v-col>
      <v-col cols="12" md="9" sm="12">
        <reactive :chart-data="ChartData"></reactive>
      </v-col>
      <v-dialog v-if="!admin && currentUser" v-model="dialog" max-width="290">
        <v-card class="elevation-16 mx-auto" width="300">
          <v-card-title style="font-size: 1rem;">
            ให้คะแนนการพยากรณ์ของระบบ
          </v-card-title>
          <v-card-text>
            <div class="text-center mt-8">
              <v-rating
                v-model="point"
                color="yellow"
                background-color="grey darken-1"
                empty-icon="$ratingFull"
                half-increments
                hover
              ></v-rating>
            </div>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions class="justify-space-between">
            <v-btn text @click="dialog = false">
              ไว้คราวหลัง
            </v-btn>
            <v-btn
              color="primary"
              text
              v-on:click="submitRating(currentUser.id)"
            >
              ให้คะแนน
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!-- ส่วนแดง ตารางและกราฟ -->
      <!-- </v-col> -->
    </v-col>
  </v-row>
</template>

<script>
// import apexchart from "@/components/Visuals/DoughnutChart";
import weatherDataService from "@/services/weatherDataService";
import axios from "axios";
import moment from "moment";
import TutorialDataService from "../../services/TutorialDataService";
import reactive from "../Visuals/reactive";
export default {
  methods: {
    submitFiles() {
      if (this.file) {
        let formData = new FormData();
        // files
        // additional data
        formData.append("file", this.file);
        weatherDataService
          .csv(formData, this.currentUser.id)
          .then(() => {
            this.ChartGetData();
            this.loadTableData();
            this.snackbar = true;
            this.text = "อัพโหลดข้อมูลเรียบร้อย";
            this.color = "success";
            this.file = [];

            this.$refs.chart.updateSeries([
              {
                data: this.ChartData,
              },
            ]);
          })
          .catch((error) => {
            console.log({ error });
          });
      } else {
        console.log("there are no files.");
        this.snackbar = true;
        this.text = "กรุณาเลือกไฟล์";
      }
    },
    async getDataall() {
      this.resetState();
      await weatherDataService
        .getAll()
        .then((response) => {
          this.days.min = response.data[0].date; //ช่วงเวลาที่เลือกได้น้อยที่สุด
          this.date11 = this.days.min; //จุดเริ่มต้นของปฎิทิน
          this.days.max = response.data[response.data.length - 1].date; //ช่วงเวลาที่เลือกได้มากที่สุด
          console.log(response.data[0]);
          //แสดงชาร์ท ไม่เกิน 100 - 150
          this.ChartData.min = response.data.map((entry) => entry.MinTemp);
          this.ChartData.max = response.data.map((entry) => entry.MaxTemp);
          this.ChartData.date = response.data.map((entry) => entry.date);
          this.loaded = true;
        })
        .catch((err) => {
          console.log(err.response.data.error);
        });
    },
    resetState() {
      this.loaded = false;
    },
    requestData() {
      if (
        this.date11 === null ||
        this.date11 === "" ||
        this.date11 === "0000-00-00" ||
        this.date11 === "undefined"
      ) {
        this.showError = true;
        return;
      }
      this.resetState();
      weatherDataService
        .findDate(this.date11, this.date22)
        .then((response) => {
          this.ChartData.min = response.data.map((entry) => entry.MinTemp);
          this.ChartData.max = response.data.map((entry) => entry.MaxTemp);
          this.ChartData.date = response.data.map((entry) => entry.date);
          this.dates = [
            `ระหว่าง ${this.ChartData.date[0]} ถึง ${
              response.data[response.data.length - 1].date
            }`,
          ];
          this.menu = false;
          this.loaded = true;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    showChart() {
      this.loadChart = true;
    },
    async ChartGetData() {
      // .get(`http://mock-json-data-generator.herokuapp.com/address?count=10`)
      // .get(`https://rmuti-surin-smartfarm.herokuapp.com/api/csv/tutorials`)
      // .get(`http://localhost:5000/api/csv/GetCSv/1`)
      if (this.currentUser) {
        this.id_get = this.currentUser.id;
      }
      if (!this.currentUser) {
        this.id_get;
      }
      await axios
        .get(
          `https://rmuti-surin-smartfarm.herokuapp.com/api/csv/GetCSv/${this.id_get}`
        )
        .then((response) => {
          console.log(response.data);
          this.numData = response.data;
          this.ChartData.min = response.data.map((entry) => entry.MinTemp);
          this.ChartData.max = response.data.map((entry) => entry.MaxTemp);
          for (var num = 0; num < response.data.length; num++) {
            this.ChartData.date.push(
              moment(response.data[num].Date)
                .locale("TH")
                .add(543, "year")
                .format("LL")
            );
          }
          if (response.data.length > 1) {
            this.loaded = true;
            this.loadChart = true;
          } else {
            console.log("ไม่มีข้อมูล");
            this.NotData = "ยังไม่มีข้อมูลการพยากรณ์";
            this.ChartData.min = [];
            this.ChartData.max = [];
            this.ChartData.date = [];
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async loadTableData() {
      await axios
        .get(
          `https://anycors-smf.herokuapp.com/https://api-rmuti.herokuapp.com/weather`
        )
        .then((response) => {
          // console.log(response.data.length);

          if (response.data.length > 1) {
            this.forcast = response.data.map(this.EditDate);
            this.loadtable = true;
            this.dialog = true;
          } else {
            this.NotDataTable = "ยังไม่มีข้อมูลการพยากรณ์";
            this.forcast = [];
          }
          // console.log(this.days.min, this.days.max);
        })
        .catch((err) => {
          // this.loadChart = true;
          console.log(err);
        });
    },
    EditDate(tutorials) {
      return {
        Date: moment(tutorials.Date)
          .locale("TH")
          .add(543, "year")
          .format("LL"),
        MaxTemp: tutorials.MaxTemp,
        MinTemp: tutorials.MinTemp,
        Rainfall: tutorials.Rainfall,
        Evaporation: tutorials.Evaporation,
        Sunshine: tutorials.Sunshine,
        WindGustSpeed: tutorials.WindGustSpeed,
        WindSpeed9am: tutorials.WindSpeed9am,
        WindSpeed3pm: tutorials.WindSpeed3pm,
        Humidity9am: tutorials.Humidity9am,
        Humidity3pm: tutorials.Humidity3pm,
        Pressure9am: tutorials.Pressure9am,
        Pressure3pm: tutorials.Pressure3pm,
        Cloud9am: tutorials.Cloud9am,
        Cloud3pm: tutorials.Cloud3pm,
        Temp9am: tutorials.Temp9am,
        Temp3pm: tutorials.Temp3pm,
        RainToday: tutorials.RainToday,
        RISK_MM: tutorials.RISK_MM,
        predicted: tutorials.predicted,
        userId: tutorials.userId,
      };
    },
    async submitRating() {
      await TutorialDataService.rating(this.currentUser.id, this.point);
    },
  },

  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    admin() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes("ROLE_ADMIN");
      }
      return false;
    },
  },

  components: {
    apexchart: () => import("@/components/Visuals/DoughnutChart"),
    Table: () => import("@/components/TableForcast"),
    reactive,
  },
  mounted() {
    // if (!this.currentUser) {
    //   this.$store.dispatch("auth/logout");
    //   this.$router.push("/register");
    // }
    this.ChartGetData();
    this.loadTableData();
    // if (!this.currentUser) {
    //   // console.log(55);
    // }
    // setInterval(() => {
    //   this.ChartGetData();
    //   this.loadTableData();
    // }, 6000);
    // this.created;
  },

  beforeDestroy() {
    clearInterval(this.ChartGetData());
  },

  data: () => {
    return {
      tbload: false,
      id_get: 1,
      dialog: false,
      point: 0,
      tab: null,
      items: ["กราฟแสดงข้อมูลอุณหภูมิ", "ตารางสภาพอากาศและผลพยากรณ์"],
      textbar: ["abc", "def"],
      file: null,
      numData: [],
      loadtable: false,
      setChart: 0,
      isActive: false,
      loadChart: false,
      menu: false,
      date11: "",
      date22: "",
      dates: [],
      dayz: "",
      selectedIndex: 5,
      days: {
        min: "",
        max: "",
        show: "",
      },
      value: null,
      day: "",
      query: "",
      city: "surin",
      country: "",
      intervalid1: "",
      datas: [],
      ChartData: {
        min: [],
        date: [],
        max: [],
      },
      loaded: false,
      showError: false,
      errorMessage: "เลือกวันที่ใหม่",
      NotData: "",
      NotDataTable: "",
      forcast: [],
      snackbar: false,
      text: "",
      timeout: 2000,
      color: "",
    };
  },
};
</script>

<style>
.active {
  color: rgb(214, 24, 103) !important;
  font-size: 1rem;
}
.progress {
  justify-content: center;
  display: flex;
  align-content: center;
  align-self: center;
  padding-top: 1rem;
  /* padding-top: 60px; */
}
</style>
